import React from "react"
import { ListProps, List, useListContext, Pagination } from "react-admin"
import { TableContainer, Table, TableHead, TableBody, TableRow, TableCell, useMediaQuery, useTheme } from "@material-ui/core"
import { useHistory } from "react-router"
import { MobilePostPagination } from '../common/Pagination'
import { ResourceName } from "../../core/ResourceName"

const BookingGrid = () => {
  const { data, ids } = useListContext()
  const history = useHistory()
  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Wydarzenie</TableCell>
            <TableCell>Rezerwacja</TableCell>
            <TableCell>Wariant</TableCell>
            <TableCell>Status</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map(id =>
            <TableRow key={id} style={{ cursor: "pointer" }}>
              <TableCell onClick={() =>
                history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${id}/show`, })
              }>
                {`${data[id]?.eventId}. ${data[id]?.eventName} ${data[id]?.eventDateStart.replace("T", " ").substring(0, 19)}`}
              </TableCell>
              <TableCell onClick={() =>
                history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${id}/show`, })
              }>{data[id]?.id}</TableCell>
              <TableCell onClick={() =>
                history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${id}/show`, })
              }>{data[id]?.variantId}</TableCell>
              <TableCell onClick={() =>
                history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${id}/show`, })
              }>{data[id]?.disabled}</TableCell>
            </TableRow>
          )}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export const BookingList = (props: ListProps) => {
  const theme = useTheme()
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={
        useMediaQuery(theme.breakpoints.up('md')) ? (
          <Pagination rowsPerPageOptions={[20, 50, 100]} />
        ) : (
          <MobilePostPagination />
        )
      }
      sort={{ field: 'id', order: 'DESC' }}
      {...props}
    >
      <BookingGrid />
    </List>
  )
}