const AccessUses = ({ id, tickets }: { id: string, tickets: number[] }) => {
  const { basePath } = useShowContext<Access>()
  return (
    <Box height="50vh">
      <ResourceContextProvider value={ResourceName.ACCESS_USINGS}>
        <AccessUsingsList
          title=" "
          basePath={basePath}
          filter={{
            ticketId: tickets.join(" "),
            accessId: id
          }}
          actions={false}
          pagination={false} />
      </ResourceContextProvider>
    </Box>
  )
}

export const AccessShow = (props: ShowProps) => {
  const translate = useTranslate()
  const [selectedTab, setSelectedTab] = useState(0)
  const { record } = useShowContext()
  const { id } = props

  const handleTabChange = (prop: number) => setSelectedTab(prop)

  return (
    <Show {...props}>
      <>
        <Grid container style={{ height: "50vh" }}>
          <Grid item xs={4}>
            <Paper style={{ height: '100%' }}>
              <TabButton
                value={0}
                selected={selectedTab}
                change={handleTabChange}
              >
                {translate('resources.access.tabs.details')}
              </TabButton>
              <TabButton
                value={1}
                selected={selectedTab}
                change={handleTabChange}
              >
                {translate('resources.access.tabs.dates')}
              </TabButton>
              <TabButton
                value={2}
                selected={selectedTab}
                change={handleTabChange}
              >
                {translate('resources.access.tabs.uses')}
              </TabButton>
            </Paper>
          </Grid>
          <Grid item xs={8} style={{ overflowY: "auto" }}>
            <Paper style={{ height: '100%' }}>
              {selectedTab === 0 && <AccessDetails />}
              {selectedTab === 1 && <AccessDates />}
              {selectedTab === 2 && <AccessUses id={id as string} tickets={record?.tickets.map((ticket: any) => ticket.id) as number[]} />}
            </Paper>
          </Grid>
        </Grid>
      </>
    </Show>
  )
}
