import {
  Edit, EditProps, TabbedForm, TextField, FormTab, TextInput,
  DateTimeInput, useTranslate, Toolbar, SaveButton, ToolbarProps
} from "react-admin"
import React, { useMemo } from "react"
import { AttachmentSelect } from "./AttachmentSelect"
import { StatusSelect } from "./StatusSelect"

const EditTitle = (record: any) => {
  const { record: { name, dateStart, id } } = record
  const translate = useTranslate()
  const date = useMemo(() => !(dateStart instanceof Date) ? dateStart.replace('T', ' ') : '', [dateStart])
  return (
    <span>{translate('resources.events.titles.edit')}: {name} {date} {id}</span>
  )
}

const EditToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
  </Toolbar>
)

export const EventEdit = (props: EditProps) => {
  const translate = useTranslate()
  return (
    <Edit {...props} title={<EditTitle />}>
      <TabbedForm toolbar={<EditToolbar />}>
        <FormTab label={translate("resources.events.tabs.general")}>
          <TextField source="id" label="ID" />
          <TextInput source="name" />
          <DateTimeInput source="dateStart" />
          <DateTimeInput source="dateEnd" />
          <StatusSelect source="status" />
        </FormTab>
        <FormTab label={translate("resources.events.tabs.devices")}>
          <AttachmentSelect source="devices" label=" " />
        </FormTab>
        <FormTab label={translate("resources.events.tabs.objects")}>
          <AttachmentSelect source="objects" label=" " />
        </FormTab>
        <FormTab label={translate("resources.events.tabs.companies")}>
          <AttachmentSelect source="companies" label=" " />
        </FormTab>
      </TabbedForm>
    </Edit>
  )
}