import {
  List, ListProps, useListContext, useTranslate, useMutation, sanitizeFetchType,
  useRefresh, UPDATE_MANY, useListSortContext
} from "react-admin"
import React from "react";
import { ButtonBase, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from "@material-ui/core"
import { ArrowDownward, ArrowUpward } from "@material-ui/icons"
import { AccessUsingDto } from "../../../core/dto/access-using.dto"
import { ResourceName } from "../../../core/ResourceName"
import { MobilePostPagination } from "../../common/Pagination";

const AccessUsingsGrid = () => {
  const translate = useTranslate()
  const { data, ids, } = useListContext()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const { currentSort, setSort } = useListSortContext()

  const patchAccess = async (access: AccessUsingDto) => {
    await mutate({
      type: sanitizeFetchType(UPDATE_MANY),
      resource: ResourceName.ACCESS_USINGS,
      payload: {
        ids: [access.id],
        data: {
          active: !access.active
        }
      }
    }, { returnPromise: true })
    refresh()
  }

  const inverseOrder = () => (currentSort.order === 'ASC' ? 'DESC' : 'ASC')

  const handleSortChange = (field: string) => {
    setSort(field, currentSort.field === field ? inverseOrder() : 'DESC')
  }

  const HeaderCell = (name: string, display: string) => (
    <div style={{ display: "flex", cursor: "pointer", textAlign: "center" }}>
      {currentSort.field === name &&
        <>
          {currentSort.order === 'ASC' ? <ArrowUpward /> : <ArrowDownward />}
        </>}
      <div> {translate(display)} </div>
    </div>
  )

  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell
              onClick={() => handleSortChange('date')}>
              {HeaderCell('date', 'resources.access-using.fields.date')}</TableCell>
            <TableCell>
              {translate('resources.access-using.fields.direction')}</TableCell>
            <TableCell>
              {translate('resources.access-using.fields.active')}</TableCell>
            <TableCell>
              {translate('resources.access-using.fields.ticketId')}</TableCell>
            <TableCell style={{ width: "20%" }} />
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <TableRow key={id}>
              <TableCell>{data[id]?.date.replace("T", " ").substring(0, 19)}</TableCell>
              <TableCell>{data[id]?.direction}</TableCell>
              <TableCell>{data[id]?.active ? translate('const.yes') : translate('const.no')}</TableCell>
              <TableCell>{data[id]?.ticketId}</TableCell>
              <TableCell>
                <ButtonBase
                  style={{ width: "100%", fontSize: "13px" }}
                  onClick={() => patchAccess(data[id] as AccessUsingDto)}>
                  {translate(`resources.access-using.actions.${data[id].active ? `deactivate` : `activate`}`)}
                </ButtonBase>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export const AccessUsingsList = (props: ListProps) => (
  <List {...props} exporter={false} pagination={<MobilePostPagination />} perPage={20} sort={{ field: "date", order: "DESC" }}>
    <AccessUsingsGrid />
  </List>
)