import { DataProvider, GetListResult, GetOneParams, GetOneResult, UpdateParams, UpdateResult, GetListParams } from "react-admin"
import { COMPANIES_URL, ENGINES_URL, EVENTS_URL, OBJECTS_URL, PALMS_URL } from "../api-urls"
import { get, patch, put, getWithPagination, getByConditions } from "../common/fetch.utils"
import { ALL_RESULTS } from "../common/filter.values"
import { EventDto } from "../dto/event.dto"
import { Event } from "./event"
import { Attachment, ShowEvent } from "./event-show"
import { PalmDto } from '../dto/palm.dto'
import { EngineDto } from "../dto/engine.dto"
import { ObjectDto } from "../dto/object.dto"
import { CompanyDto } from "../dto/company.dto"
import { EventDevices } from "../dto/event-devices.dto"
import { EventCompanies } from "../dto/event-companies.dto"
import { DateTime } from "../common/date-time"
import { EventStatus } from "../enum/EventStatus"
import { mapSortEventParam } from './event-sort.mapper'

type EventUpdateRequest = {
  id: number,
  name?: string,
  dateStart?: DateTime,
  dateEnd?: DateTime,
  status?: EventStatus
}

interface EventListParams extends GetListParams {
  readonly sort: {
    readonly field: keyof Event
    readonly order: 'ASC' | 'DESC'
  }
  readonly filter:
  {
    readonly name: string
  }
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { name },
      sort: { field, order },
      pagination: { page, perPage }
    }: EventListParams,
  ): Promise<GetListResult<Event>> => {
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortEventParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)
    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const { data: events, range: { total } } = await getWithPagination<EventDto[]>(`${EVENTS_URL}/GetByConditions/`, `${name ? `e=>e.Name.Contains("${name}")` : ALL_RESULTS}?${pathParams}`)

    const data = events.map((event) => ({
      id: event.id,
      name: event.name,
      dateStart: event.dateStart,
      dateEnd: event.dateEnd,
      status: event.status
    }))

    return Promise.resolve({
      data,
      total
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<ShowEvent>> => {
    const event = await get<EventDto>(EVENTS_URL, `/${id}`)

    const palms = await getByConditions<PalmDto>(PALMS_URL, `p=>true`)
    const engines = await getByConditions<EngineDto>(ENGINES_URL, `e=>true`)
    const objects = await getByConditions<ObjectDto>(OBJECTS_URL, `o=>true`)
    const companies = await get<CompanyDto[]>(COMPANIES_URL, `/c=>true`)

    const linkedDevices = await get<EventDevices>(EVENTS_URL, `/GetDevices/${event.id}`)
    const linkedCompanies = await get<EventCompanies>(EVENTS_URL, `/GetCompanies/${event.id}`)

    return {
      data: {
        id: event.id,
        name: event.name,
        dateStart: event.dateStart,
        dateEnd: event.dateEnd,
        status: event.status,
        devices: palms.map((palm) =>
        ({
          id: palm.id,
          name: palm.name,
          linked: linkedDevices.verifierDevicesIds.includes(palm.id),
          categoryId: palm.categoryId,
          type: 'palm' as 'palm' | 'engine'
        })).concat(engines.map((engine) =>
        ({
          id: engine.id,
          name: engine.name,
          linked: linkedDevices.verifierDevicesIds.includes(engine.id),
          categoryId: engine.categoryId,
          type: 'engine' as 'palm' | 'engine'
        }))),
        objects: objects.map((object) => ({ id: object.id, name: object.name, linked: event.objectsIds.includes(object.id) })),
        companies: companies.map((company) => ({ id: company.id, name: company.name, linked: linkedCompanies.companiesIds.includes(company.id) }))
      }
    }
  },
  update: async (
    resource,
    { id, data, previousData }: UpdateParams
  ): Promise<UpdateResult> => {
    const { status: previousStatus, devices: previousDevices, objects: previousObjects, companies: previousCompanies } = previousData
    const { name, dateStart, dateEnd, status, devices, objects, companies } = data

    const pendingUpdate = patch<EventUpdateRequest, EventDto>(EVENTS_URL, {
      id: parseInt(id as string, 10),
      name,
      dateStart: new Date(new Date(dateStart).getTime() - (new Date(dateStart).getTimezoneOffset() * 60000)).toJSON() as DateTime,
      dateEnd: new Date(new Date(dateEnd).getTime() - (new Date(dateEnd).getTimezoneOffset() * 60000)).toJSON() as DateTime
    })

    if (previousStatus !== status) {
      if (status === EventStatus.ACTIVE) put<undefined, { "jobId": string }>
        (`${EVENTS_URL}/ActivateAsyncJob/${id}`, undefined)
      else put<undefined, { "jobId": string }>
        (`${EVENTS_URL}/DeactivateAsyncJob/${id}`, undefined)
    }

    if (previousDevices.filter((device: Attachment) => device.linked).length !== devices.filter((device: Attachment) => device.linked).length)
      put<{ "eventId": number, "verifierDevicesIds": number[] }, { "eventId": number, "verifierDevicesIds": number[] }>
        (`${EVENTS_URL}/SetDevices`, { "eventId": id as number, "verifierDevicesIds": devices.filter((device: Attachment) => device.linked).map((device: Attachment) => device.id) })

    if (previousObjects.filter((object: Attachment) => object.linked).length !== objects.filter((object: Attachment) => object.linked).length)
      put<{ "eventId": number, "objectsIds": number[] }, { "eventId": number, "objectsIds": number[] }>
        (`${EVENTS_URL}/SetObjects`, { "eventId": id as number, "objectsIds": objects.filter((object: Attachment) => object.linked).map((object: Attachment) => object.id) })

    if (previousCompanies.filter((company: Attachment) => company.linked).length !== companies.filter((company: Attachment) => company.linked).length)
      put<{ "eventId": number, "companiesIds": number[] }, { "eventId": number, "companiesIds": number[] }>
        (`${EVENTS_URL}/SetCompanies`, { "eventId": id as number, "companiesIds": companies.filter((company: Attachment) => company.linked).map((company: Attachment) => company.id) })

    const event = await pendingUpdate
    return Promise.resolve({
      data: event
    })
  }
} as DataProvider

export default provider