import ReceiptIcon from '@material-ui/icons/Receipt'
import ViewComfyIcon from '@material-ui/icons/ViewComfy'
import PersonIcon from '@material-ui/icons/Person'
import React from 'react'
import { Admin, Resource } from 'react-admin'
import themeReducer from './core/common/theme.reducer'
import { providersFactory } from './core/providers.factory'
import { ResourceName } from './core/ResourceName'
import { AppLayout } from './main/common/AppLayout'
import { CustomerDetailsView } from './main/customer-details/CustomerDetailsView'
import { CustomerInfoList } from './main/customer-info/CustomerInfoList'
import { Dashboard } from './main/Dashboard'
import { EventEdit } from './main/events/EventEdit'
import { EventList } from './main/events/EventList'
import { EventShow } from './main/events/EventShow'
import { LogList } from './main/logs/LogList'
import { OverallView } from './main/stats/overall-view/OverallView'
import { OverallViewList } from './main/stats/overall-view/OverallViewList'
import { AccessUsingsList } from './main/ticket-details/accesses/AccessUsingsList'
import { TicketDetailsView } from './main/ticket-details/TicketDetailsView'
import { TicketInfoList } from './main/ticket-info/TicketInfoList'
import routes from './main/common/routes'

const App = () => {
  const { authProvider, dataProvider, i18nProvider } = providersFactory(process.env.NODE_ENV)
  return (
    <Admin
      dashboard={Dashboard}
      authProvider={authProvider}
      dataProvider={dataProvider}
      i18nProvider={i18nProvider}
      customReducers={{theme: themeReducer}}
      customRoutes={routes}
      layout={AppLayout}
    >
      <Resource
        icon={ReceiptIcon}
        name={ResourceName.TICKET_INFO}
        list={TicketInfoList}
      />
      <Resource
        name={ResourceName.TICKET_DETAILS}
        show={TicketDetailsView}
      />
      <Resource
        name={ResourceName.LOGS}
        list={LogList}
      />
      <Resource
        icon={ViewComfyIcon}
        name={ResourceName.STATS_OVERALL_VIEW_LIST}
        list={OverallViewList}
      />
      <Resource
        icon={ViewComfyIcon}
        name={ResourceName.STATS_OVERALL_VIEW}
        show={OverallView}
      />
      <Resource
        name={ResourceName.TICKET_LOGS} />
      <Resource
        name={ResourceName.ACCESSES} />
      <Resource
        name={ResourceName.ACCESS_USINGS}
        list={AccessUsingsList}
      />
      <Resource
        name={ResourceName.EVENTS}
        list={EventList}
        show={EventShow}
        edit={EventEdit} />
      <Resource
        icon={PersonIcon}
        name={ResourceName.CUSTOMER_INFO}
        list={CustomerInfoList}
      />
      <Resource
        name={ResourceName.CUSTOMER_DETAILS}
        show={CustomerDetailsView} />
      <Resource
        name={ResourceName.BOOKINGS} />
    </Admin>
  )
}

export default App
