import {
  List, ListProps, FilterProps, Filter, TextInput, useListContext, Datagrid,
  TextField, DateField, FunctionField, useTranslate, Record, Pagination
} from "react-admin"
import React from "react"
import { ResourceName } from "../../core/ResourceName"

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput source="name" alwaysOn />
  </Filter>
)

const EventGrid = () => {
  const { loading } = useListContext()
  const translate = useTranslate()
  return (
    <Datagrid size="small" loaded={!loading} rowClick={(id) => `${ResourceName.EVENTS}/${id}/show`}>
      <TextField source="id" label="ID" />
      <TextField source="name" />
      <DateField source="dateStart" showTime />
      <DateField source="dateEnd" showTime />
      <FunctionField resource="events" label={translate("resources.events.fields.status")}
        render={(r: Record | undefined) =>
          (translate(`resources.enums.eventStatus.${r?.status.toLowerCase()}`))} />
    </Datagrid>
  )
}

export const EventList = (props: ListProps) => (
  <List {...props} actions={false} pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />} filters={<ListFilters />} perPage={20}>
    <EventGrid />
  </List>
)