import { InputProps, useTranslate, useInput, FunctionField, Record } from "react-admin"
import React from "react"
import { Button, Box, Typography, makeStyles, createStyles } from "@material-ui/core"
import { useSelector } from "react-redux"
import { EventStatus } from "../../core/enum/EventStatus"
import { ThemeState } from "../../core/common/theme.reducer"

const useStyles = makeStyles(() => {
  const darkMode = useSelector((state: ThemeState) => state.theme.darkMode === 'dark')
  return (
    createStyles({
      box: {
        display: "flex",
        flexDirection: 'column',
        width: '234px',
        alignItems: 'center',
        padding: '12px',
        paddingBottom: '5px',
        borderBottom: 'solid 1px',
        borderColor: darkMode ? '#c6c6c6' : '#8e8e8e',
        background: darkMode ? '#3f3f3f' : '#f5f5f5', borderTopLeftRadius: '5px', borderTopRightRadius: '5px',
        paddingTop: '4px'
      },
    })
  )
}
)

export const StatusSelect = (props: InputProps) => {
  const {
    input: { onChange, value }
  } = useInput(props)
  const translate = useTranslate()
  const classes = useStyles()
  return (
    <Box className={classes.box} >
      <Typography color='textSecondary' variant='caption' style={{ alignSelf: 'end' }}>
        {translate('resources.events.fields.status')}
      </Typography>
      <FunctionField label="resources.events.fields.status" variant="body1"
        style={{ margin: '5px', color: value === EventStatus.IN_TRANSITION ? 'red' : 'inherit' }}
        render={(record: Record | undefined) =>
          `${translate(`resources.enums.eventStatus.${value.toLowerCase()}`)}
            ${record?.status === value ? '' : `(${translate('const.changed')})`}`} />
      {value === EventStatus.IN_TRANSITION ?
        <Button disabled variant='contained' style={{ width: "100%" }}>
          {translate(`resources.events.actions.activate`)}
        </Button> :
        <Button variant='outlined' style={{ width: "100%" }}
          onClick={() => onChange(value === EventStatus.ACTIVE ? EventStatus.INACTIVE : EventStatus.ACTIVE)} >
          {translate(`resources.events.actions.${value === EventStatus.INACTIVE ? 'activate' : 'deactivate'}`)}
        </Button>
      }
    </Box>
  )
}