import { BookingDto } from "../dto/booking.dto";
import { EventDto } from "../dto/event.dto";
import { Booking } from "./booking";

export const mapToBookings = (
  {
    id,
    type,
    disabled,
    activeFrom,
    activeTo,
    eventId,
    seatId,
    blockId,
    customerId,
    variantId,
    ticketsIds,
    createdAt,
    modifiedAt
  }: BookingDto,
  events: EventDto[]
): Booking => {
  const foundEvent = events.find((event) => event.id === eventId)
  return({
    id,
    type,
    disabled,
    activeFrom,
    activeTo,
    eventId,
    seatId,
    blockId,
    customerId,
    variantId,
    ticketsIds,
    createdAt,
    modifiedAt,
    eventName: foundEvent?.name,
    eventDateStart: foundEvent?.dateStart
  })
}