import { useInput, useTranslate, InputProps } from "react-admin"
import React, { useMemo, useState } from "react"
import {
  TableCell, TableContainer, TableHead, TableRow, Table, TableBody, Box, FormControl, InputLabel,
  Checkbox, Button, TablePagination, TextField, ButtonGroup, Select, MenuItem, Menu, IconButton
} from "@material-ui/core"
import { HelpOutline, FilterList, Close } from "@material-ui/icons"
import { Device } from '../../core/events/event-show'

const NoResult = () => {
  const translate = useTranslate()
  return (
    <Box style={{
      width: "100%", display: "flex", flexDirection: "column",
      alignItems: "center", paddingTop: "10px", paddingBottom: "10px"
    }}>
      <HelpOutline fontSize="large" style={{ margin: "5px" }} />
      <div>{translate('const.noResults')}</div>
    </Box>
  )
}

export const AttachmentSelect = (props: InputProps) => {
  const { source } = props
  const translate = useTranslate()

  const {
    input: { onChange, value }
  } = useInput(props)

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const orgValue = useMemo<Device[]>(() => value, [])

  //page and number of rows per page for pagination
  const [pagination, setPagination] = useState
    <{ page: number, perPage: number }>
    ({ page: 0, perPage: 20 })

  //object containing most variables associated with filtering
  const [filter, setFilter] = useState<{
    display: { name: boolean, category: boolean, type: boolean },
    values: { name: string, category: string, type: 'palm' | 'engine' | '' },
    menu: {
      open: boolean,
      anchor: any
    }
  }>({
    display: { name: true, category: false, type: false },
    values: { name: '', category: '', type: '' },
    menu: {
      open: false,
      anchor: null
    }
  })

  const onChangePage = (event: React.MouseEvent<HTMLButtonElement> | null, p: number) => {
    setPagination({ ...pagination, page: p })
    window.scrollTo(0, 130)
  }

  const onChangeRowsPerPage = (event: any) => {
    setPagination({ perPage: event?.target.value, page: 0 })
    window.scrollTo(0, 130)
  }

  //function filtering the displayed data
  const display = (arr: Device[]) => {
    let dispArr = arr.filter((dev) => dev.name.toLowerCase()
      .includes(filter.values.name.toLowerCase()))
    if (filter.values.category !== '')
      dispArr = dispArr.filter((dev) =>
        dev.categoryId === parseInt(filter.values.category, 10))
    if (filter.values.type !== '')
      dispArr = dispArr.filter((dev) =>
        dev.type === filter.values.type)
    return dispArr
  }

  const displayWithPagination = (arr: Device[]) =>
    display(arr).slice(pagination.page * pagination.perPage, (pagination.page + 1) * pagination.perPage)

  //filters displayed above table
  const displayFilters = () => {
    const onFilterClose = (type: 'type' | 'category') => {
      setFilter({
        ...filter,
        display: {
          ...filter.display,
          [type]: false
        },
        values: {
          ...filter.values,
          [type]: ''
        }
      })
      setPagination({ ...pagination, page: 0 })
    }

    const onFilterSubmit = (type: 'name' | 'type' | 'category', payload: any) => {
      setFilter({
        ...filter,
        values: {
          ...filter.values,
          [type]: payload
        }
      })
      setPagination({ ...pagination, page: 0 })
    }

    const closeButton = (type: 'type' | 'category') => (
      <span style={{
        display: "inline-flex", height: "100%",
        justifyContent: "center", alignItems: "center"
      }}>
        <IconButton onClick={() => onFilterClose(type)}>
          <Close style={{ border: "solid thin", borderRadius: "50%" }}
            fontSize="small" />
        </IconButton>
      </span>
    )

    return (
      <Box padding={1}>
        {filter.display.name &&
          <TextField label={translate('resources.events.fields.name')} variant="filled"
            onChange={e => onFilterSubmit('name', e.target.value)} />}

        {source === 'devices' &&
          <>
            {filter.display.category &&
              <span style={{ padding: "5px", whiteSpace: "nowrap" }}>
                {closeButton('category')}
                <TextField label={translate('resources.events.fields.category')} variant="filled"
                  onChange={e => onFilterSubmit('category', e.target.value)} />
              </span>}

            {filter.display.type &&
              <span style={{ padding: "5px", whiteSpace: "nowrap" }}>
                {closeButton('type')}
                <FormControl variant="filled">
                  <InputLabel>
                    {translate('resources.events.fields.type')}
                  </InputLabel>
                  <Select value={filter.values.type} style={{ width: "219px" }}
                    onChange={e => onFilterSubmit('type', e.target.value)}>
                    <MenuItem value=''>
                      {translate('resources.events.fields.type')}
                    </MenuItem>
                    <MenuItem value='palm'>
                      {translate('resources.enums.deviceType.palm')}
                    </MenuItem>
                    <MenuItem value='engine'>
                      {translate('resources.enums.deviceType.engine')}
                    </MenuItem>
                  </Select>
                </FormControl>
              </span>}
          </>}
      </Box>
    )
  }

  //actions displayed on top of page (select all, deselect all, filter menu)
  const actions = () => {
    const onLinked = (state: boolean) => {
      onChange(value.map((device: Device) => {
        if (displayWithPagination(value).includes(device))
          return { ...device, linked: state }
        return device
      }))
    }

    const onMenuOpen = (e: any) => setFilter({
      ...filter,
      menu: {
        open: true,
        anchor: e?.currentTarget
      }
    })

    const onMenuClose = () => setFilter({
      ...filter,
      menu: {
        open: false,
        anchor: null
      }
    })

    const onMenuClick = (type: 'category' | 'type') => setFilter({
      ...filter,
      display: {
        ...filter.display,
        [type]: true
      },
      menu: {
        open: false,
        anchor: null
      }
    })

    return (
      <Box style={{ height: "48px", display: "flex" }}>
        <Box style={{ width: "70%", display: "inline-flex" }}>
          <ButtonGroup>
            <Button
              onClick={() => onLinked(true)}>
              {translate('const.selectAll')}
            </Button>
            <Button
              onClick={() => onLinked(false)}>
              {translate('const.deselectAll')}
            </Button>
          </ButtonGroup>
        </Box>
        <Box style={{ width: "30%", display: "inline-flex", flexDirection: "row-reverse" }}>
          {source === 'devices' && Object.values(filter.display).includes(false) &&
            <IconButton onClick={onMenuOpen}
              style={{ alignSelf: "flex-end", marginRight: "0px" }}>
              <FilterList />
            </IconButton>}
          <Menu anchorEl={filter.menu.anchor} open={filter.menu.open} onClose={onMenuClose}>
            {!filter.display.category &&
              <MenuItem onClick={() => onMenuClick('category')}>
                {translate('resources.events.fields.category')}
              </MenuItem>}
            {!filter.display.type &&
              <MenuItem onClick={() => onMenuClick('type')}>
                {translate('resources.events.fields.type')}
              </MenuItem>
            }
          </Menu>
        </Box>
      </Box>
    )
  }

  return (
    <>
      {actions()}
      <br />
      {displayFilters()}
      {display(value).length > 0 ?
        <>
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell style={source === 'devices' ? { width: "45%" } : { width: "90%" }}>
                    {translate(`resources.events.fields.${source}`)}
                  </TableCell>
                  {source === 'devices' && <TableCell style={{ width: "45%" }}>
                    {translate('resources.events.fields.type')}
                  </TableCell>}
                  <TableCell style={{ width: "10%" }}>
                    {translate('resources.events.fields.status')}
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {displayWithPagination(value).map((device: Device, index: number) => (
                  <TableRow key={device.id}
                    style={displayWithPagination(orgValue)[index].linked ? { background: "green" } : {}}>
                    <TableCell>{device.name}</TableCell>
                    {source === 'devices' &&
                      <TableCell>
                        {translate(`resources.enums.deviceType.${device.type}`)}
                      </TableCell>}
                    <TableCell>
                      <Checkbox checked={device.linked}
                        onClick={() => onChange(value.map((dev: Device) => {
                          if (dev.name === device.name)
                            return ({ ...dev, linked: !dev.linked })
                          return dev
                        }))} />
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            component="div"
            rowsPerPageOptions={[10, 20, 50]}
            count={display(value).length}
            page={pagination.page}
            rowsPerPage={pagination.perPage}
            onChangePage={onChangePage}
            onChangeRowsPerPage={onChangeRowsPerPage}
            labelRowsPerPage={translate('ra.navigation.rows_per_page')}
            labelDisplayedRows={
              ({ from, to, count }) => `${from}-${to} // ${count}`
            }
          />
        </> :
        <NoResult />
      }
    </>
  )
}