import { ResourceContextProvider, ShowProps, Show, TabbedShowLayout, Tab, TextField, useShowContext, DateField } from "react-admin"
import React, { useMemo } from "react"
import { Box } from "@material-ui/core"
import { LogList } from '../logs/LogList'
import { ResourceName } from "../../core/ResourceName"
import { BookingList } from "./CustomerBookingsList"

const CustomerBookingsView = () => {
  const { record, basePath } = useShowContext()
  return (
    <Box paddingY={2}>
      <ResourceContextProvider value={ResourceName.BOOKINGS}>
        <BookingList
          basePath={basePath}
          filter={{ id: record?.bookingsIds.join(",") }}
          filters={undefined}
          actions={false}
          title=" "
        />
      </ResourceContextProvider>
    </Box>
  )
}

const CustomerLogsView = () => {
  const { record, basePath } = useShowContext()
  return (
    <Box paddingY={2}>
      <ResourceContextProvider value={ResourceName.TICKET_LOGS}>
        <LogList
          basePath={basePath}
          filter={{ bookingId: record?.bookingsIds.join(",") }}
          filters={undefined}
          actions={false}
          title=" "
        />
      </ResourceContextProvider>
    </Box>
  )
}

const CustomerTabs = () => {
  const { record } = useShowContext()
  const bookingPresent = useMemo(() => record?.bookingsIds.length > 0, [record])
  return (
    <TabbedShowLayout>
      <Tab label="Profil">
        <TextField source="firstname" label="resources.customer-details.fields.firstname" />
        <TextField source="lastname" label="resources.customer-details.fields.lastname" />
        {record?.company && <TextField source="company" label="resources.customer-details.fields.company" />}
        {record?.birthdate && <DateField source="birthdate" label="resources.customer-details.fields.birthdate" />}
        {record?.documentType && <TextField source="documentType" label="resources.customer-details.fields.documentType" />}
        {record?.pesel ?
          <TextField source="pesel" label="resources.customer-details.fields.pesel" /> :
          <TextField source="documentNumber" label="resources.customer-details.fields.documentNumber" />
        }
        {record?.nationality && <TextField source="nationality" label="resources.customer-details.fields.nationality" />}
        {record?.gender && <TextField source="gender" label="resources.customer-details.fields.gender" />}
      </Tab>
      {bookingPresent &&
        <Tab label="Rezerwacje">
          <CustomerBookingsView />
        </Tab>
      }
      {bookingPresent &&
        <Tab label="Logi">
          <CustomerLogsView />
        </Tab>
      }
    </TabbedShowLayout>
  )
}

export const CustomerDetailsView = (props: ShowProps) => (
  <Show {...props}>
    <CustomerTabs />
  </Show>
)