import React, { useState } from "react"
import {
  Show, ShowProps, TabbedShowLayout, Tab, TextField, DateField,
  useShowContext, useTranslate, FunctionField, Record
} from "react-admin"
import {
  Checkbox, TableContainer, Table, TablePagination,
  TableHead, TableBody, TableRow, TableCell
} from '@material-ui/core'
import { Attachment, ShowEvent, Device } from "../../core/events/event-show"

const ShowTitle = (record: any) => {
  const { record: { name, dateStart, id } } = record
  const translate = useTranslate()
  return (
    <span>{translate('resources.events.titles.show')}: {!!name && name} {dateStart.replace("T", " ")} {!!id && id}</span>
  )
}

const LinkRow = ({ record }: { record: Device }) => {
  const translate = useTranslate()
  return (
    <TableRow>
      <TableCell>{record.name}</TableCell>
      {!!record.type &&
        <TableCell>{translate(`resources.enums.deviceType.${record.type}`)}</TableCell>}
      <TableCell>
        <Checkbox checked={record.linked} disabled />
      </TableCell>
    </TableRow>
  )
}

const AttachmentShow = ({ type }: { type: 'devices' | 'objects' | 'companies' }) => {
  const { record } = useShowContext<ShowEvent>()
  const translate = useTranslate()
  const [page, setPage] = useState(0)
  const [perPage, setPerPage] = useState(10)

  const handlePageChange = (e: any, p: number) => setPage(p)
  const handlePerPageChange = (e: any) => {
    setPerPage(e?.target.value)
    setPage(0)
  }

  return (
    <>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell style={type === 'devices' ? { width: "45%" } : { width: "90%" }}>{translate(`resources.events.fields.${type}`)}</TableCell>
              {type === 'devices' &&
                <TableCell style={{ width: "45%" }}>{translate(`resources.events.fields.type`)}</TableCell>}
              <TableCell style={{ width: "10%" }}>{translate(`resources.events.fields.status`)}</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {!!record && record[type] !== undefined && record[type].slice(page * perPage, (page + 1) * perPage).map((attachment: Attachment) => (
              <LinkRow record={attachment as Device} key={attachment.id} />
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination component="div" count={!!record && record[type] ? record[type].length as number : 0}
        rowsPerPage={perPage} page={page} onChangePage={handlePageChange}
        onChangeRowsPerPage={handlePerPageChange} rowsPerPageOptions={[10, 20, 50]}
        labelRowsPerPage={translate('ra.navigation.rows_per_page')}
        labelDisplayedRows={({ from, to, count }) => `${from}-${to} // ${count}`} />
    </>
  )
}

export const EventShow = (props: ShowProps) => {
  const translate = useTranslate()
  return (
    <Show {...props} title={<ShowTitle />}>
      <TabbedShowLayout>
        <Tab label={translate("resources.events.tabs.general")}>
          <TextField source="id" label="ID" />
          <TextField source="name" />
          <DateField source="dateStart" showTime />
          <DateField source="dateEnd" showTime />
          <FunctionField resource="events" label={translate("resources.events.fields.status")}
            render={(r: Record | undefined) =>
              (translate(`resources.enums.eventStatus.${r?.status.toLowerCase()}`))} />
        </Tab>
        <Tab label={translate("resources.events.tabs.devices")}>
          <AttachmentShow type="devices" />
        </Tab>
        <Tab label={translate("resources.events.tabs.objects")}>
          <AttachmentShow type="objects" />
        </Tab>
        <Tab label={translate("resources.events.tabs.companies")}>
          <AttachmentShow type="companies" />
        </Tab>
      </TabbedShowLayout>
    </Show>
  )
}